.list{
    margin: 0;
    list-style: none;
    padding: 0;
    width: 400px;
}
.name{
    /* margin-right: 30px; */
    margin: 0 30px 0 5px;
    justify-self: start;
}
.item{
     display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 5px;
    border-bottom: 1px solid #cecece;
}
.item:not(:last-child){
    margin-bottom: 10px;
   
}
.btn{
    margin-left: 10px;
    padding: 3px;
     border: 1px solid #cecece;
     border-radius: 5px;
    box-shadow: 1px 4px 6px rgba(0, 0, 0, 0.16), 0px 4px 4px rgba(0, 0, 0, 0.06),
    0px 1px 1px rgba(0, 0, 0, 0.12);
}
.btn:hover,
.btn:focus{
    /* background-color: #dd3131; */
    
    color: #fff;
    transform: scale(1.1);
}

.edit:hover{
fill: #f3b42c
}

.del:hover{
fill: #dd3131
}